<template>
    <div class="Payment">
        <main>
            <div class="weui-cells weui-cells_form">
                <div class="weui-cell">
                    <div class="weui-cell__hd">
                        <label class="weui-label">预约人</label>
                    </div>
                    <div class="weui-cell__bd">
                        {{orderinfo.customerName}}
                    </div>
                </div>
                <div class="weui-cell">
                    <div class="weui-cell__hd">
                        <label class="weui-label">预约电话</label>
                    </div>
                    <div class="weui-cell__bd">
                        {{orderinfo.customerPhone}}
                    </div>
                </div>
                <div class="weui-cell">
                    <div class="weui-cell__hd">
                        <label class="weui-label">入住时间</label>
                    </div>
                    <div class="weui-cell__bd">
                        {{orderinfo.roomInTime}}
                    </div>
                </div>
                <div class="weui-cell">
                    <div class="weui-cell__hd">
                        <label class="weui-label">离店时间</label>
                    </div>
                    <div class="weui-cell__bd">
                        {{orderinfo.roomOutTime}}
                    </div>
                </div>
                <div class="weui-cell">
                    <div class="weui-cell__hd">
                        <label class="weui-label">预订房型</label>
                    </div>
                    <div class="weui-cell__bd">
                        {{orderinfo.categoryName}}
                    </div>
                </div>
                <div class="weui-cell">
                    <div class="weui-cell__hd">
                        <label class="weui-label">预订数量</label>
                    </div>
                    <div class="weui-cell__bd">
                        {{orderinfo.number}}间
                    </div>
                </div>
                <div class="weui-cell">
                    <div class="weui-cell__hd">
                        <label class="weui-label">支付方式</label>
                    </div>
                    <div class="weui-cell__bd">
                        {{orderinfo.payType|PaymentMethod}}
                    </div>
                </div>
                <div class="weui-cell">
                    <div class="weui-cell__hd">
                        <label class="weui-label">备注</label>
                    </div>
                    <div class="weui-cell__bd">
                        <template v-if="orderinfo.remark">{{orderinfo.remark}}</template>
                        <template v-else>无</template>
                    </div>
                </div>
                <div class="weui-cell">
                    <div class="weui-cell__bd">
                        {{orderinfo.name}}
                    </div>
                </div>
                <div class="weui-cells information">
                    <a class="weui-cell weui-cell_access" :href="'tel:'+orderinfo.phone">
                        <div class="weui-cell__hd">
                            <svg class="icon" aria-hidden="true">
                                <use xlink:href="#icon-dianhuahaoma"></use>
                            </svg>
                        </div>
                        <div class="weui-cell__bd">
                            <p>{{orderinfo.phone}}</p>
                        </div>
                        <div class="weui-cell__ft"></div>
                    </a>
                    <a class="weui-cell weui-cell_access" href="javascript:;" @click="mapfn">
                        <div class="weui-cell__hd">
                            <svg class="icon" aria-hidden="true">
                                <use xlink:href="#icon-weizhi"></use>
                            </svg>
                        </div>
                        <div class="weui-cell__bd">
                            <p>
                                <p>{{orderinfo.address}}</p>
                            </p>
                        </div>
                        <div class="weui-cell__ft"></div>
                    </a>
                </div>
            </div>
        </main>
        <div class="totalSum">
            <p>订单总额：
                <span class="Price">￥{{orderinfo.realPrice|money}}</span>
                <a class="minutia" @click="dialog1=true">明细</a>
            </p>
            <div class="btn">
                <a href="javascript:;" class="weui-btn weui-btn_primary" @click="payFn">立即支付</a>
                <a href="javascript:;" class="weui-btn weui-btn_primary" @click="cancelOrderfn">取消订单</a>
            </div>
        </div>
        <!-- 弹框 -->
        <div class="js_dialog" v-if="dialog1">
            <div class="weui-mask" @click="dialog1=false"></div>
            <div class="weui-dialog Paymentdetails">
                <i class="weui-icon-cancel" @click="dialog1=false"></i>
                <div class="weui-dialog__hd">
                    费用明细
                </div>
                <div class="weui-dialog__bd">
                    <h4 class="title mt15">费用明细</h4>
                    <div class="weui-flex">
                        <div class="weui-flex__item">
                            <div class="placeholder">
                                房费*{{orderinfo.number}}
                            </div>
                        </div>
                        <div class="weui-flex__item right">
                            <div class="placeholder">
                                +￥{{orderinfo.roomCost|money}}
                            </div>
                        </div>
                    </div>
                    <div class="weui-flex">
                        <div class="weui-flex__item">
                            <div class="placeholder">
                                押金*{{orderinfo.number}}
                            </div>
                        </div>
                        <div class="weui-flex__item right">
                            <div class="placeholder">
                                +￥{{orderinfo.deposit*orderinfo.number|money}}
                            </div>
                        </div>
                    </div>
                    <div class="weui-flex" v-if="orderinfo.couponsDiscount">
                        <div class="weui-flex__item">
                            <div class="placeholder">
                                优惠券
                            </div>
                        </div>
                        <div class="weui-flex__item right">
                            <div class="placeholder">
                                -￥{{orderinfo.couponsDiscount|money}}
                            </div>
                        </div>
                    </div>
                    <div class="weui-flex" v-if="orderinfo.integralDiscount">
                        <div class="weui-flex__item">
                            <div class="placeholder">
                                积分
                            </div>
                        </div>
                        <div class="weui-flex__item right">
                            <div class="placeholder">
                                -￥{{orderinfo.integralDiscount|money}}
                            </div>
                        </div>
                    </div>
                    <div class="weui-flex">
                        <div class="weui-flex__item">
                            <div class="placeholder">
                                合计
                            </div>
                        </div>
                        <div class="weui-flex__item right">
                            <div class="placeholder">
                                +￥{{orderinfo.realPrice|money}}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script type='text/ecmascript-6'>
import { common } from "@/assets/js/request.js";
import { PaymentMethod, money } from "@/assets/js/filters.js";
export default {
  data() {
    return {
      hotelId: sessionStorage.getItem("hotelId"),
      dialog1: false,
      orderId: this.$route.params.orderId,
      orderinfo: "",
      falg: true
    };
  },
  mounted() {
    common.paymentOrder({
      Suffix: sessionStorage.getItem("hotelId") + "/order/" + this.orderId,
      fn: data => {
        this.orderinfo = data;
        if (
          this.orderinfo.activityDetailVo &&
          this.orderinfo.activityDetailVo.activityType == 2
        ) {
        } else {
          this.orderinfo.roomInTime = this.orderinfo.roomInTime.slice(0, 10);
          this.orderinfo.roomOutTime = this.orderinfo.roomOutTime.slice(0, 10);
        }
        this.orderinfo.roomCost =
          Number(this.orderinfo.realPrice) +
          Number(this.orderinfo.integralDiscount) +
          Number(this.orderinfo.couponsDiscount) -
          Number(this.orderinfo.deposit * this.orderinfo.number);
      }
    });
  },
  methods: {
    mapfn() {
      location.href =
        "http://apis.map.qq.com/tools/routeplan/eword=" +
        this.orderinfo.address +
        "&epointx=" +
        this.orderinfo.longitude +
        "&epointy=" +
        this.orderinfo.latitude +
        "?referer=mapqq&key=GQKBZ-BKZW6-ZCVSQ-MD5GX-MYNBQ-LSBPK";
    },
    payFn() {
      if (this.falg) {
        this.falg = false;
        var _this = this;
        common.getconfig({
          fn: data => {
            location.href =
              data.hostUrl +
              "mobile/78CDF1/room/" +
              sessionStorage.getItem("hotelId") +
              "/pay/" +
              this.orderId;
            setTimeout(() => {
              _this.falg = false;
            }, 100);
          }
        });
      }
    },
    cancelOrderfn() {
      common.cancelOrder({
        Suffix: sessionStorage.getItem("hotelId") + "/cancel/" + this.orderId,
        fn: data => {
          this.Toast({
            message: "取消成功",
            duration: 2000
          });
          var _this = this;
          setTimeout(function() {
            _this.$router.go(-1);
          }, 1000);
        }
      });
    }
  }
};
</script>

<style scoped lang='less' rel='stylesheet/less'>
.Payment {
  display: flex;
  flex-flow: column;
  min-height: 100vh;
}

main {
  flex: 1;
  background: #f4f4f4;
}
</style>



